import success from './voices/success.mp3'
import failure from './voices/failure.mp3'
import tick from './voices/tick.mp3'


export default [
    {
        name: 'success',
        url: success
    },
    {
        name: 'failure',
        url: failure
    },
    {
        name: 'tick',
        url: tick
    },

];


